import React, { Component } from 'react';
import Sample from './Sample';
import './App.css';

class App extends Component {
  render() {

    const fonts = ['Play'];
    const fontWeights = [ {regular:'regular',x:'bold'}]
    const xStyle = ['lowercase']
    const greens = ['#74dd02',"#80c342","#14b04d","#3eb649","#69bd45","#1B858D","#1ABA9E","#00FBA3","#00D78B","#00A269","#008355","#006340"]



  const samples = fonts.map( f => {
    return fontWeights.map( w => {
      return xStyle.map( s => {
        return greens.map( g => {
            return <Sample fontName={f} weights={w} xstyle={s} green={g} key={f+w.regular+s+g}/>
          });
        });
      });
    });
    return (
      <div className="App">
        
      </div>
    );
  }
}

export default App;
